import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { AppService } from 'src/app/app.service';
import { lect } from '../../lect/config.lect';
import { client } from '../config.client';

@Component({
  selector: 'app-data',
  templateUrl: './data.component.html',
  styleUrls: ['./data.component.scss']
})
export class DataComponent implements OnInit {
  @Output() openNew: EventEmitter<client | null> = new EventEmitter();
  clients: client[] = [];
  searchText: string;
  constructor(public app: AppService) { }
  ngOnInit() {
    this.getClientsData();
  }
  showItem(item: client) {
    this.openNew.emit(item);
  }
  getClientsData() {
    this.app.wait();
    this.app.http.get<client[]>(`/api/clients/${this.app.user.id}`).subscribe(
      res => {
        this.clients = res;
        this.app.wait(false);
      }, err => { }
    );
  }
  deleteclient(a: client) {
    this.app.dialogService.confirm(
      "تاكيد حذف العميل",
      "هل انت متاكد من حذف العميل",
      "نعم",
      "لا").subscribe(
        res => {
          if (res == true) {
            this.app.http.delete(`/api/clients/${this.app.user.id}/${a.id}`).subscribe(
              res => {
                this.ngOnInit();
              },
              err => {
                this.app.wait(false);
              }
            )
          }
        }
      )
  }
  deleteLect(data: lect) {
    this.app.dialogService.confirm(
      "تاكيد حذف الجلسة",
      "هل انت متاكد من حذف الجلسة",
      "نعم",
      "لا").subscribe(
        res => {
          if (res == true) {
            this.app.http.delete(`/api/lects/${this.app.user.id}/${data.clientId}/${data.id}`).subscribe(
              res => {
                this.ngOnInit();
              },
              err => {
                this.app.wait(false);
              }
            )
          }
        }
      )
  }
}
