import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppService } from 'src/app/app.service';
import { client } from '../config.client';

@Component({
  selector: 'app-new-client',
  templateUrl: './new-client.component.html',
  styleUrls: ['./new-client.component.scss']
})
export class NewClientComponent implements OnInit {
  @Output() openNew: EventEmitter<null> = new EventEmitter();
  @Input("data") data: client;
  clientForm: FormGroup;
  ngOnInit() {
    this.clientForm = new FormGroup({
      name: new FormControl("", [Validators.required]),
      tel: new FormControl("", [Validators.required]),
      address: new FormControl("", [Validators.required]),
      tn: new FormControl(""),
      ty: new FormControl(""),
      tc: new FormControl(""),
      img: new FormControl(""),
      lec: new FormControl([]),
      id: new FormControl(""),
    });
    if (this.data != undefined) {
      this.clientForm.setValue({
        address: "",
        tn: "",
        ty: "",
        tc: "",
        ...this.data
      });
    }
  }
  constructor(public app: AppService) { }
  updateClient() {
    if (!this.clientForm.valid) {
      this.app.log("بعض البيانات مفقودة");
      return;
    }
    this.app.wait();
    if (this.data != undefined) {
      this.app.http.put(`api/clients/${this.app.user.id}/${this.data.id}`, this.clientForm.value).subscribe(
        res => {
          this.app.wait(false);
          this.openNew.emit();
        }, err => { }
      );
    } else {
      this.app.http.post(`api/clients/${this.app.user.id}`, this.clientForm.value).subscribe(
        res => {
          this.app.wait(false);
          this.openNew.emit();
        }, err => { }
      );
    }
  }
}
